	module.exports = {"data":{"talkJson":{"description":"How is an expert OO developer to improve on his craft? By learning from other paradigms! These six principles of the functional style can apply to OO. Some of these principles are part of good practice already; some express patterns both old and new; all give us different ways of thinking about problems. For developers without expertise in functional programming, examples in Java provide new techniques for writing clear, quality code.","meta":{"duration":"PT51M14S"},"presenter":{"name":null},"slug":"jessica-kerr-functional-principles-for-object-oriented-development","source":{"id":"pMGY9ViIGNU","provider":"youtube"},"tag":["Jessica","Kerr","JDD"],"title":"Jessica Kerr \"Functional Principles for Object Oriented Development\""},"allTalkJson":null},"pathContext":{"slug":"jessica-kerr-functional-principles-for-object-oriented-development","tags":["Jessica","Kerr","JDD"]}}

